export const setIndispos = (state, indispos) => {
  state.indispos = indispos;
};

export const deleteIndispos = (state, idIndispo) => {
  state.indispos = state.indispos.filter(i => +i.id !== +idIndispo);
};

export const removeUnavailabilitiesToValidate = (state, idIndispo) => {
  state.unavailabilitiesToValidate = state.unavailabilitiesToValidate.filter(
    i => +i.id !== +idIndispo
  );
};

export const setUnavailabilities = (state, unavailabilities) => {
  state.unavailabilities = unavailabilities;
};

export const addUnavailability = (state, unavailability) => {
  state.unavailabilities.push(unavailability);
};

export const setUnavailabilitiesToValidate = (
  state,
  unavailabilitiesToValidate
) => {
  state.unavailabilitiesToValidate = unavailabilitiesToValidate;
};

export const setUnavailabilityReasons = (state, unavailabilityReasons) => {
  state.unavailability_reasons = unavailabilityReasons;
};

export const setUnavailabilityReasonsForMe = (state, unavailabilityReasons) => {
  state.unavailability_reasons_for_me = unavailabilityReasons;
};

export const setFamilyUnavailabilityReasonsForMe = (
  state,
  familyUnavailabilityReasons
) => {
  state.family_unavailability_reasons_for_me = familyUnavailabilityReasons;
};

export const updateAllUsers = (state, users) => {
  state.allUsers = users;
};

export const updateTree = (state, tree) => {
  state.treeStore = tree;
};

export const setManager = (state, manager) => {
  state.manager = manager;
};

export const setPublicHolidays = (state, publicHolidays) => {
  state.publicHolidays = publicHolidays;
};

export const setStandbyTime = (state, standbyTime) => {
  state.standbyTime = standbyTime;
};

export const setValidator = (state, validator) => {
  state.validator = validator;
};

export const setPerimeters = (state, perimeters) => {
  state.perimeters = perimeters;
};

export const setSelectedGids = (state, selectedGids) => {
  state.selectedGids = selectedGids;
};

export const incrementRequestNumber = state => {
  state.requestNumber += 1;
};

export const setManagedUsers = (state, managedUsers) => {
  state.managedUsers = managedUsers;
};



