export const setDialog = ({ state }, dialog) => {
  state.dialog = dialog;
};
export const setClipped = ({ state }, clipped) => {
  state.clipped = clipped;
};
export const setDrawer = ({ state }, drawer) => {
  state.drawer = drawer;
};
export const setMini = ({ state }, mini) => {
  state.mini = mini;
};
export const setFixed = ({ state }, fixed) => {
  state.fixed = fixed;
};
export const setMiniVariant = ({ state }, miniVariant) => {
  state.miniVariant = miniVariant;
};
export const setItems = ({ state }, items) => {
  state.items = items;
};



