import services from "@/services";
import types from "./types";
import {
  dateFormat,
  returnUnavailabilityWording,
  dateFormatandWording,
  sortAndFilterUsers,
  getManagedMembersFromList,
  removeDuplicates
} from "../../utils/indispos";

// Fonction recursive de renomage des noeuds car le tree view ne regarde que les noeuds "children" (bug du treeoption en mode store)
function traverse(o, tab, entitie) {
  const option = o;
  const tabUsers = tab;

  if (!option.prenom) {
    // C'est une entitiÃ©
    option.nomLong = `${option.id} ${option.nom}`;
    // RecursivitÃ© sur les entitÃ©s filles
    if (option.children) {
      for (let i = 0; i < option.children.length; i += 1) {
        traverse(option.children[i], tabUsers, entitie);
      }
    }
  } else {
    // C'est un utilisateur avec gid
    option.gid_collaborateur = option.id;
    option.nomLong = `${option.nom} ${option.prenom}`;
    tabUsers[option.id] = option;
  }
}

export const getUnavailabilityReasonsForMe = ({ commit, rootState }) =>
  services.employees
    .getEmployeesUnavailabilityReasonsService(rootState.user.gid)
    .then((reasons = []) => {
      const reasonsAltered = reasons.length
        ? reasons.map(reason => {
            const newreason = {
              lib: `${reason.libelle_abs_non_affectee} - ${reason.libelle_abs}`,
              conditionSubmission: {
                code_abs: reason.code_abs,
                validation: reason.validation_needed === "O",
                justificatif: reason.proof_needed === "O",
                solde: reason.balance_related === "O"
              }
            };
            return Object.assign(reason, newreason);
          })
        : [];

      const reasonsAlteredFamilyNone = reasons.length
        ? reasons.map(reason => {
            const newfamilyreason = {
              libelle_abs_non_affectee: `${reason.libelle_abs_non_affectee}`,
              code_abs_non_affectee: `${reason.code_abs_non_affectee}`
            };
            return newfamilyreason;
          })
        : [];

      const reasonsAlteredFamily = removeDuplicates(
        reasonsAlteredFamilyNone,
        "code_abs_non_affectee"
      );

      reasonsAlteredFamily.sort((family1, family2) => {
        if (
          family1.libelle_abs_non_affectee < family2.libelle_abs_non_affectee
        ) {
          return -1;
        } else if (
          family1.libelle_abs_non_affectee > family2.libelle_abs_non_affectee
        ) {
          return 1;
        }
        return 0;
      });
      reasonsAltered.sort((reason1, reason2) => {
        if (
          reason1.libelle_abs_non_affectee < reason2.libelle_abs_non_affectee
        ) {
          return -1;
        } else if (
          reason1.libelle_abs_non_affectee > reason2.libelle_abs_non_affectee
        ) {
          return 1;
        }
        return 0;
      });
      commit(types.UNAVAILABILITY_REASONS_FOR_ME, reasonsAltered);
      commit(types.FAMILY_UNAVAILABILITY_REASONS_FOR_ME, reasonsAlteredFamily);
    });

export const getUnavailabilityReasons = ({ commit, state }) =>
  new Promise((resolve, reject) => {
    if (!state.unavailability_reasons.length) {
      services.unavailabilityReasons
        .getUnavailabilityReasonsService()
        .then(reasons => {
          if (!reasons.length) {
            reject("No unavailability reasons received");
          } else {
            const reasonsAltered = reasons.map(reason => {
              const newreason = {
                lib: `${reason.libelle_abs_non_affectee} - ${
                  reason.libelle_abs
                }`,
                conditionSubmission: {
                  code_abs: reason.code_abs,
                  validation: reason.validation_needed === "O",
                  justificatif: reason.proof_needed === "O",
                  solde: reason.balance_related === "O"
                }
              };
              return Object.assign(reason, newreason);
            });
            reasonsAltered.sort((reason1, reason2) => {
              if (
                reason1.libelle_abs_non_affectee <
                reason2.libelle_abs_non_affectee
              ) {
                return -1;
              } else if (
                reason1.libelle_abs_non_affectee >
                reason2.libelle_abs_non_affectee
              ) {
                return 1;
              }
              return 0;
            });
            commit(types.UNAVAILABILITY_REASONS, reasonsAltered);
            resolve(reasonsAltered);
          }
        });
    } else {
      resolve(state.unavailability_reasons);
    }
  }).catch(e => {
    console.log(e);
  });

export const getPublicHolidaysSearch = (
  { commit, state },
  { requestNumber, users, startDate, endDate }
) => {
  services.publicHolidays
    .getPublicHolidaysSearchService(users, startDate, endDate)
    .then(joursFeries => {
      if (requestNumber === state.requestNumber) {
        const formatJoursFeries = {};
        if (joursFeries)
          joursFeries.forEach(jrs => {
            formatJoursFeries[jrs.gid] = jrs.holidays;
          });
        commit(types.SET_PUBLICHOLIDAYS, formatJoursFeries);
      }
    });
};

export const getStandByTimeSearch = (
  { commit, state },
  { requestNumber, users, startDate, endDate }
) => {
  services.standbyTime
    .getStandbyTimeSearchService(users, startDate, endDate)
    .then(astreintes => {
      if (requestNumber === state.requestNumber) {
        const formatAstreintes = {};
        if (astreintes)
          astreintes.forEach(ast => {
            formatAstreintes[ast.gid] = ast.standby_time;
          });
        commit(types.SET_STANDBYTIME, formatAstreintes);
      }
    });
};

export const getUsersUnavailabilitiesSearch = (
  { commit, rootState },
  { requestNumber, users, startDate, endDate }
) => {
  services.unavailabilities
    .getUnavailabilitiesSearchService(
      rootState.user.gid,
      rootState.user.managedUsers,
      users,
      startDate,
      endDate,
      rootState.global.unavailability_reasons
    )
    .then(indispos => {
      // Controle si la requete correspond a la derniere envoyee
      if (requestNumber === rootState.global.requestNumber)
        commit(types.SET_INDISPOS, indispos);
    });
};

export const deleteUnavailabilities = ({ commit }, idIndispo) => {
  services.unavailabilities
    .deleteUnavailabilitiesService(idIndispo)
    .then(response => {
      if (response.status === 200) {
        commit(types.DELETE_INDISPO, idIndispo);
      }
    });
};

export const validateUnavailabilities = ({ commit, rootState }, indispo) => {
  services.unavailabilities
    .validateUnavailabilitiesService(
      indispo.etag,
      rootState.user.gid,
      indispo.id
    )
    .then(response => {
      if (response.status === 200) {
        commit(types.REMOVE_UNAVAILABILITIES_VALIDATE, indispo.id);
      }
    });
};

export const refuseUnavailabilities = ({ commit, rootState }, indispo) => {
  services.unavailabilities
    .refuseUnavailabilitiesService(indispo.etag, rootState.user.gid, indispo.id)
    .then(response => {
      if (response.status === 200) {
        commit(types.REMOVE_UNAVAILABILITIES_VALIDATE, indispo.id);
      }
    });
};

export const getEmployeesUnavailabilities = ({ state, commit, rootState }) => {
  getUnavailabilityReasons({ state, commit, rootState }).then(
    unavailabilities => {
      services.employees
        .getEmployeesUnavailabilitiesService(rootState.user.gid)
        .then(response => {
          const items = response.data.items;
          if (items.length) {
            for (let i = 0; i < items.length; i += 1) {
              dateFormat(items[i]);
              returnUnavailabilityWording(items[i], unavailabilities);
            }
          }
          commit(types.UNAVAILABILITIES, items);
        });
    }
  );
};

export const getUnavailabilitiesToValidate = ({ state, commit, rootState }) => {
  getUnavailabilityReasons({ state, commit, rootState }).then(
    unavailabilitiesManager => {
      services.unavailabilities
        .getUnavailabilitiesToValidateService()
        .then(resp => {
          if (
            resp.data &&
            resp.data.items.length &&
            unavailabilitiesManager.length
          ) {
            const promises = resp.data.items.map(unavailability =>
              services.unavailabilities.getUnavailabilityService(
                unavailability.id
              )
            );

            Promise.all(promises).then(values => {
              const tab = values.map(value => {
                const val = value.data.items[0];
                dateFormat(val);
                returnUnavailabilityWording(val, unavailabilitiesManager);
                return Object.assign(val, {
                  etag: value.headers.etag
                });
              });
              commit(types.UNAVAILABILITIES_VALIDATE, tab);
            });
          }
        });
    }
  );
};

export const saveUnavailability = (
  { state, commit, rootState },
  unavailability
) => {
  getUnavailabilityReasons({ state, commit, rootState }).then(
    unavailabilitiesManager => {
      const unavailabilitySend = unavailability;
      unavailabilitySend.gid_collaborateur = rootState.user.gid;
      return services.unavailabilities
        .postUnavailabilitiesService(unavailabilitySend)
        .then(response => {
          if (response.status === 200) {
            commit(
              types.ADD_UNAVAILABILITY,
              dateFormatandWording(unavailabilitySend, unavailabilitiesManager)
            );
          }
        });
    }
  );
};

export const loadPerimetersTree = ({ commit, rootState }) => {
  const gid = rootState.user.gid;
  const allUsers = {};
  services.employees.getEmployeesPerimetersService(gid).then(perimeters => {
    if (perimeters.length) {
      for (let i = 0; i < perimeters.length; i += 1) {
        if (perimeters[i].children) {
          // On renomme les noeuds entities en children
          traverse(perimeters[i], allUsers, rootState.user);
        }
      }
    }
    commit(types.UPDATE_TREE, perimeters);
    commit(types.UPDATE_ALL_USERS, allUsers);
  });
};

export const loadEmployeeFromGid = ({ commit }, gid) =>
  services.employees.getEmployeesGidService(gid).then(employees => {
    commit(types.SET_VALIDATOR, employees[0]);
  });

export const loadEmployeesListFromGid = ({ commit, rootState }, users) =>
  new Promise((resolve, reject) => {
    if (users.length) {
      services.employees
        .getEmployeesListService(users)
        .then(employees => {
          commit(
            types.SET_MANAGED_USERS,
            getManagedMembersFromList(rootState.user.gid, employees)
          );
          commit(
            types.SET_PERIMETERS,
            sortAndFilterUsers(employees, rootState.user.gid)
          );
          resolve(employees);
        })
        .catch(e => {
          reject(e);
        });
    } else {
      commit(types.SET_PERIMETERS, []);
      resolve([]);
    }
  });

export const incrementRequestNumber = ({ commit }) => commit(types.INCREMENT);

export const setSelectedGids = ({ commit, rootState }, selectedGids) => {
  commit(
    types.SET_SELECTED_GIDS,
    sortAndFilterUsers(selectedGids, rootState.user.gid).map(u => ({
      gid: u.gid_collaborateur
    }))
  );
};



