/* eslint-disable */
import apis from "../../apis";

export default {
  getStandbyTimeSearchService(
    users,
    startDate,
    endDate
  ) {
    return apis.standbyTime
      .getStandbyTimeSearchApi(users, startDate.toISOString().split('.')[0], endDate.toISOString().split('.')[0])
      .then(
        response => response.data.items
)
  }
};



