import ApiClient from "@engie/apiclient";

const API_URL = "https://postman-echo.com/";

// Headers CORS ajoutÃ©s pour appels externes.
const client = new ApiClient({
  baseURL: API_URL,
  headers: {
    "Access-Control-Allow-Origin": "*",
    "Access-Control-Allow-Methods": "GET, POST, PATCH, PUT, DELETE, OPTIONS",
    "Access-Control-Allow-Headers": "Origin, Content-Type, X-Auth-Token"
  }
});

export default {
  getAllFoos() {
    return client.get("/get?foo1=bar1&foo2=bar2");
  },

  getFooById(id) {
    return client.get("/get", {
      params: {
        foo: id
      }
    });
  },

  postFoo(foo) {
    return client.post("/post", { foo });
  }
};



