import { sortAndFilterUsers } from "../../utils/indispos";

export const getGid = state => state.gid;

export const getManagedUsers = state => state.managedUsers;

export const getMembersSameTeams = state => state.members;

export const getEmployeesPerimeters = state => {
  sortAndFilterUsers(state.perimeters, state.gid);
  return state.perimeters;
};

export const getEmployeesGid = state => state.employee;

export const getAllGidsTeam = state => state.allGidsTeam;



