import services from "@/services";
import {
  getManagedMembersFromList,
  sortAndFilterUsers
} from "@/utils/indispos";
import types from "./types";

// eslint-disable-next-line
export const loadMembersSameTeams = (
  { commit, rootState },
  pageNumber = 1,
  numberPerPage = 15
) =>
  // S'il n'y a pas de gids d'utilisateurs de la team dans le store on envoie la requete pour les recuperer
  rootState.user.allGidsTeam.length
    ? services.employees
        .getEmployeesListService(
          rootState.user.allGidsTeam.slice(
            (pageNumber - 1) * numberPerPage,
            pageNumber * numberPerPage
          )
        )
        .then(members => {
          if (members.length) {
            commit(
              types.SET_MANAGED_USERS,
              getManagedMembersFromList(rootState.user.gid, members)
            );
            commit(types.SET_MEMBERS_TEAMS, members);
          }
          return members.map(u => ({ gid: u.gid_collaborateur }));
        })
    : services.employees
        .getEmployeesTeamsService(rootState.user.gid)
        .then(members => {
          // On recupÃ¨re et on stocke la liste de Gids des utilisateurs de l'Ã©quipe par defaut, triÃ© par gid, nom et prenom
          const allGidsTeam = sortAndFilterUsers(
            members,
            rootState.user.gid
          ).map(u => ({ gid: u.gid_collaborateur }));

          commit(types.SET_ALL_GID_TEAM, allGidsTeam);

          // On extrait la portion des utilisateurs affichÃ©e dans le calendrier
          const pageGidTeam = allGidsTeam.slice(
            (pageNumber - 1) * numberPerPage,
            pageNumber * numberPerPage
          );

          return services.employees.getEmployeesListService(pageGidTeam);
        })
        .then(members => {
          if (members.length) {
            commit(
              types.SET_MANAGED_USERS,
              getManagedMembersFromList(rootState.user.gid, members)
            );
            commit(types.SET_MEMBERS_TEAMS, members);
          }
          return members.length
            ? members.map(u => ({ gid: u.gid_collaborateur }))
            : [];
        });

export const loadEmployeesGid = ({ commit, rootState }) =>
  services.employees
    .getEmployeesGidService(rootState.user.gid)
    .then(employees => {
      commit(types.SET_EMPLOYEE, employees[0]);
    });

export const loadGid = ({ state }) => {
  const oktaToken = localStorage.getItem("okta-token-storage");
  if (oktaToken) {
    state.gid = JSON.parse(oktaToken).idToken.claims.preferred_username.split(
      "@"
    )[0];
  }
};

export const loadSelectedMembersFromGids = (
  { commit, rootState },
  pageNumber = 1,
  numberPerPage = 15
) =>
  services.employees
    .getEmployeesListService(
      rootState.global.selectedGids.slice(
        (pageNumber - 1) * numberPerPage,
        pageNumber * numberPerPage
      )
    )
    .then(members => {
      if (members.length) {
        commit(
          types.SET_MANAGED_USERS,
          getManagedMembersFromList(rootState.user.gid, members)
        );
        commit(types.SET_MEMBERS_TEAMS, members);
      }
      return members.map(u => ({ gid: u.gid_collaborateur }));
    });



