/* eslint-disable */
import apis from "../../apis";
import { fromEventDBtoEventCalendar, returnDate } from "../../utils/indispos";

export default {
  getUnavailabilitiesSearchService(
    gid,
    managedUsers,
    users,
    startDate,
    endDate,
    unavailabilityReasons
  ) {
    return apis.unavailabilities
      .getUnavailabilitiesSearchApi(
        users,
        startDate.toISOString().split(".")[0],
        endDate.toISOString().split(".")[0]
      )
      .then(
        indispos =>
          indispos.data.items
            ? fromEventDBtoEventCalendar(
                indispos.data,
                gid,
                managedUsers,
                unavailabilityReasons
              )
            : []
      );
  },
  getUnavailabilityService(idUnavailabilitie) {
    return apis.unavailabilities
      .getUnavailabilityApi(idUnavailabilitie)
      .then(response => response);
  },
  getUnavailabilitiesToValidateService() {
    return apis.unavailabilities
      .getUnavailabilitiesToValidateApi()
      .then(response => response);
  },
  postUnavailabilitiesService(unavailability) {
    unavailability.date_debut = returnDate(
      unavailability.start_date,
      unavailability.start_time
    );
    unavailability.date_fin = returnDate(
      unavailability.end_date,
      unavailability.end_time
    );
    delete unavailability.start_time;
    delete unavailability.end_time;
    delete unavailability.start_date;
    delete unavailability.end_date;
    return apis.unavailabilities.postUnavailabilitiesApi(unavailability);
  },
  deleteUnavailabilitiesService(idIndispo) {
    return apis.unavailabilities.deleteUnavailabilitiesApi(idIndispo);
  },
  validateUnavailabilitiesService(etag, gid, idIndispo) {
    return apis.unavailabilities.editUnavailabilitiesApi(etag, gid, idIndispo, "VALIDEE");
  },
  refuseUnavailabilitiesService(etag, gid, idIndispo) {
    return apis.unavailabilities.editUnavailabilitiesApi(etag, gid, idIndispo, "REFUSEE");
  }
};



