import apis from "../../apis";

export default {
  getEmployeesUnavailabilitiesService(gid) {
    return apis.employees
      .getEmployeesUnavailabilitiesApi(gid)
      .then(response => response);
  },

  getEmployeesTeamsService(gid) {
    return apis.employees
      .getEmployeesTeamsApi(gid)
      .then(response => response.data.items);
  },

  getEmployeesGidService(gid) {
    return apis.employees
      .getEmployeesGidApi(gid)
      .then(response => response.data.items);
  },

  getEmployeesListService(users) {
    return apis.employees
      .getEmployeesListApi(users)
      .then(response => response.data.items);
  },

  getEmployeesPerimetersService(gid) {
    return apis.employees
      .getEmployeesPerimetersApi(gid)
      .then(response => response.data.items);
  },

  getEmployeesUnavailabilityReasonsService(gid) {
    return apis.employees
      .getEmployeesUnavailabilityReasonsApi(gid)
      .then(response => response.data.items);
  }
};



