import foo from "./foo";
import employees from "./employees";
import unavailabilities from "./unavailabilities";
import unavailabilityReasons from "./unavailabilityReasons";
import publicHolidays from "./publicHolidays";
import standbyTime from "./standbyTime";

const services = {
  foo,
  employees,
  unavailabilities,
  unavailabilityReasons,
  publicHolidays,
  standbyTime
};

Object.keys(services).forEach(service => {
  Object.keys(services[service]).forEach(f => {
    if (services[service][f] instanceof Function) {
      const originalFunction = services[service][f];
      services[service][f] = (...args) =>
        originalFunction(...args)
          .then(r => {
            const event = new Event("online");
            window.dispatchEvent(event);
            return r;
          })
          .catch(err => {
            const event = new Event("offline");
            window.dispatchEvent(event);
            // Hotfix rafraichissement
            if (err.response && err.response.status === 403) {
              localStorage.clear();
              location.reload();
            }
            return err;
          });
    }
  });
});
export default services;



