import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

export default new Router({
  mode: "history",
  routes: [
    {
      path: "/",
      meta: {
        requiresAuth: true
      },
      component: () => import("@/layouts/default"),
      children: [
        {
          path: "",
          component: () => import("@/pages/default")
        }
      ]
    }
  ]
});



