import Vue from "vue";
import Vuex from "vuex";
import global from "./global";
import layout from "./layout";
import user from "./user";

Vue.use(Vuex);

const store = new Vuex.Store({
  modules: {
    global,
    layout,
    user
  }
});

export default store;



