/* eslint-disable */
import apis from "../../apis";

export default {
  getPublicHolidaysSearchService(
    users,
    startDate,
    endDate
  ) {
    return apis.publicHolidays
      .getPublicHolidaysSearchApi(users, startDate.toISOString().split('.')[0], endDate.toISOString().split('.')[0])
      .then(
        response => response.data.items
)
  }
};



