import ApiClient from "@engie/apiclient";

const client = new ApiClient({
  baseURL: process.env.API_LOCAL || process.env.API
});

export default {
  getUnavailabilitiesSearchApi(users, start, end) {
    return client.post(`/unavailabilities/search`, {
      employees: users,
      period: [
        {
          start_date: start,
          end_date: end
        }
      ]
    });
  },

  getUnavailabilityApi(idUnavailabilitie) {
    return client.get(`/unavailabilities/${idUnavailabilitie}`);
  },

  getUnavailabilitiesToValidateApi() {
    return client.get(`/unavailabilities/tovalidate`);
  },

  postUnavailabilitiesApi(unavailabilitie) {
    return client.post("/unavailabilities", unavailabilitie);
  },

  deleteUnavailabilitiesApi(idUnavailabilitie) {
    return client.delete(`/unavailabilities/${idUnavailabilitie}`);
  },

  editUnavailabilitiesApi(etag, gid, idUnavailabilitie, statut) {
    return client.put(
      `/unavailabilities/${idUnavailabilitie}`,
      {
        statut
      },
      {
        headers: {
          put: {
            // can be common or any other method
            "If-Match": etag,
            "X-API-SUB": gid
          }
        }
      }
    );
  }
};



