// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import EngieVueAuthentication from "@engie-vue/authentication";
import Vuetify from "vuetify";
import VueSweetalert2 from "vue-sweetalert2";
import VeeValidate from "vee-validate";
import "vuetify/dist/vuetify.min.css";
import LiquorTree from "liquor-tree";
import ElementUI from "element-ui";
import lang from "element-ui/lib/locale/lang/fr";
import locale from "element-ui/lib/locale";
import "element-ui/lib/theme-chalk/index.css";
import App from "./App";
import router from "./router";
import store from "./store";
import i18n from "./i18n";
import "./assets/css/default.css";
import "./assets/css/holiday-us.css";
import onlineManager from "./utils/online";

locale.use(lang);
Vue.use(ElementUI);

Vue.use(EngieVueAuthentication, {
  oauth2: {
    callback: "/implicit/callback",
    issuer: "https://engie.okta-emea.com",
    client_id: "0oa1qfpp2sp0MsVRW0i7",
    redirect_uri: `${location.origin}/implicit/callback`,
    router
  }
});

Vue.use(Vuetify, {
  theme: {
    primary: "#0097c1" /* indigo */,
    secondary: "#003b70" /* bleu  */,
    info: "#00BCD4" /* cyan */,
    warning: "#FF9800" /* orange */,
    success: "#4CAF50" /* vert */,
    error: "#F44336" /* rouge */
  }
});

Vue.use(VueSweetalert2);

Vue.use(VeeValidate);

Vue.config.productionTip = false;

Vue.use(LiquorTree);

/* eslint-disable no-new */
new Vue({
  el: "#app",
  router,
  components: {
    App
  },
  template: "<App/>",
  store,
  i18n
});
onlineManager(store);



