import moment from "moment";

const fromEventDBtoEventCalendar = (
  events,
  gid,
  managedUsers,
  unavailabilities
) =>
  events.items.map(evt => {
    const isManaged =
      managedUsers.includes(evt.gid_collaborateur) ||
      evt.gid_collaborateur === gid;
    return {
      id: evt.id,
      me: evt.gid_collaborateur === gid,
      managed: managedUsers.includes(evt.gid_collaborateur),
      id_user: evt.gid_collaborateur,
      startDate: evt.date_debut,
      endDate: evt.date_fin,
      name: evt.nom,
      statut: evt.statut,
      classes:
        isManaged && unavailabilities
          ? [
              `unavailabilities_reason_${
                evt.statut === "AVALIDER" ? "waiting_" : ""
              }${
                unavailabilities.filter(
                  u => u.code_abs === evt.indisponibilite
                )[0].code_abs_non_affectee
              }`
            ]
          : [],
      forname: evt.prenom,
      title: isManaged ? evt.indisponibilite : "",
      lib:
        isManaged && unavailabilities.length
          ? unavailabilities.filter(u => u.code_abs === evt.indisponibilite)[0]
              .libelle_abs
          : ""
    };
  });

const dateFormat = o => {
  const option = o;
  option.startDate = moment(option.date_debut).valueOf();
  option.endDate = moment(option.date_fin).valueOf();
  option.createdDate = moment(option.date_created).valueOf();

  return option;
};

const returnUnavailabilityWording = (o, unavailabilityReasons) => {
  const option = o;
  if (unavailabilityReasons) {
    unavailabilityReasons.forEach(element => {
      if (element.code_abs === o.indisponibilite) {
        option.unavailabilityWording = element.libelle_abs;
      }
    });
  }
  return option;
};

const dateFormatandWording = (o, unavailabilityReasons) => {
  const option = o;
  dateFormat(option);
  returnUnavailabilityWording(option, unavailabilityReasons);
  return option;
};

const removeDuplicates = (myArr, prop) =>
  myArr.filter(
    (obj, pos, arr) =>
      arr.map(mapObj => mapObj[prop]).indexOf(obj[prop]) === pos
  );

const sortAndFilterUsers = (users = [], gid) => {
  // On trie par ordre alphabetique des prenoms + noms
  if (users.length) {
    users.sort((a, b) =>
      (a.code_entite + a.nom + a.prenom).localeCompare(
        b.code_entite + b.nom + b.prenom
      )
    );

    // On met l'utilisateur connectÃ© en tÃªte de liste
    users.forEach((element, index) => {
      if (element.gid_collaborateur === gid) {
        users.splice(index, 1);
        users.splice(0, 0, element);
      }
    });
  }
  return users;
};

const returnDate = (date, hour) => {
  const [yearDebut, monthDebut, dayDebut] = date.split("-");
  // eslint-disable-next-line
  const [hourDebut, minutesDebut, _] = hour.split(":");

  return new Date(yearDebut, monthDebut - 1, dayDebut, hourDebut, minutesDebut)
    .toISOString()
    .replace(/\.000/, "");
};

const getSplitDateTimeWithAddedHours = (date, time, hours) => {
  const end = moment(date + time, "YYYY-MM-DDHH:mm").add(hours, "hours");
  return [end.format("YYYY-MM-DD"), end.format("HH:mm")];
};

const getManagedMembersFromList = (gid, members = []) =>
  members.length
    ? members
        .filter(
          member =>
            member.gid_manager_n1 === gid || member.gid_manager_n2 === gid
        )
        .map(member => member.gid_collaborateur)
    : [];
// eslint-disable-next-line
export {
  returnDate,
  getSplitDateTimeWithAddedHours,
  fromEventDBtoEventCalendar,
  dateFormat,
  returnUnavailabilityWording,
  sortAndFilterUsers,
  getManagedMembersFromList,
  dateFormatandWording,
  removeDuplicates
};



