export const setMembersSameTeams = (state, members) => {
  state.members = members;
};

export const setEmployee = (state, employee) => {
  state.employee = employee;
};

export const setGid = (state, gid) => {
  state.gid = gid;
};

export const setManagedUsers = (state, managedUsers) => {
  state.managedUsers = managedUsers;
};

export const setPerimeters = (state, perimeters) => {
  state.perimeters = perimeters;
};

export const setAllGidsTeam = (state, gids) => {
  state.allGidsTeam = gids;
};



