import ApiClient from "@engie/apiclient";

const client = new ApiClient({
  baseURL: process.env.API_LOCAL || process.env.API
});

export default {
  getStandbyTimeSearchApi(users, start, end) {
    return client.post(`/standby_time/search`, {
      employees: users,
      period: [
        {
          start_date: start,
          end_date: end
        }
      ]
    });
  }
};



