import ApiClient from "@engie/apiclient";

const client = new ApiClient({
  baseURL: process.env.API_LOCAL || process.env.API
});

export default {
  getEmployeesUnavailabilitiesApi(gid) {
    return client.get(`/employees/${gid}/unavailabilities`);
  },

  getEmployeesUnavailabilityReasonsApi(gid) {
    return client.get(`/employees/${gid}/unavailability_reasons`);
  },

  getEmployeesTeamsApi(gid) {
    return client.get(`employees/${gid}/teams`);
  },

  getEmployeesPerimetersApi(gid) {
    return client.get(`employees/${gid}/perimeters`);
  },

  getEmployeesGidApi(gid) {
    return client.get(`employees/${gid}`);
  },

  getEmployeesListApi(users) {
    return client.post(`employees/getlist`, {
      employees: users
    });
  }
};



